import { styled } from "twin.macro";
import React from 'react'
/** @jsxImportSource @emotion/react */

const Inner = styled.path`
  fill-opacity: 0;
  will-change: fill-opacity;
  animation: drawInner 2s linear forwards;
  @keyframes drawInner {
    100% {
      fill-opacity: 1;
    }
  }
`;

const Letter = styled.path`
  --stroke-dasharray-letter: 351;
  --stroke-dashoffset-letter: 350;
  fill-opacity: 0;
  animation-delay: 4s;
  will-change: fill-opacity;
  animation: drawLetter 2s linear forwards;
  stroke-dasharray: var(--stroke-dasharray-letter);
  stroke-dashoffset: var(--stroke-dashoffset-letter);
  @keyframes drawLetter {
    80% {
      fill-opacity: 0;
    }
    100% {
      stroke-dashoffset: 0;
      fill-opacity: 1;
    }
  }
`;

