export const SocialIcon = ({ name }) => {
  switch (name) {
    case "github":
      return <GitHub />;

    case "twitter":
      return <Twitter />;

    case "gitlab":
      return <GitLab />;

    case "linkedin":
      return <LinkedIn />;

    case "medium":
      return <Medium />;

    case "instagram":
      return <Instagram />;

    case "facebook":
      return <Facebook />;

    default:
      return <Link />;
  }
};
